const NotificationForm = () => {
  const dispatch = useDispatch();

    // ----------------------------------
    // DB  (URLid)
    // ----------------------------------
    // window.location.pathname U RL 
    // .split  /product/edit' s(id)
    let id = window.location.pathname.split('/notification/form') [1];
    console.log("Before split /", id)
    
    // id 
    if(id !== "") {
        // id  .split  / 
        id = id.split('/')[1]
        console.log("After split /",id);
    }
    

  const [type, setType] = useState(""),
        [subject, setSubject] = useState(""),
        [text, setText] = useState("")

  const types =  [
    {id: "select1", name: ''},
    {id: "service", name: ''},
    {id: "other", name: ''},
];

const inputSubject = useCallback((event) => {
  setSubject(event.target.value)
},[setSubject]);

const inputText = useCallback((event) => {
  setText(event.target.value)
},[setText]);
   

        // DB (DB useState State )
        useEffect(() => {
          // id 
          if (id !== "") {
              // productsCollection  products  id 
            db.collection('contacts').doc(id).get()
            // snapshot  id
              .then(snapshot => {
                  // data  snapshot .data 
                  const data = snapshot.data();
                  // useState    data 
                  setType(data.type);
                  setSubject(data.subject);
                  setText(data.text);
              })
          }
      }, [id])

      
 return(

<div className="common__item">
<div className="common__center">
    <Container>
    <Sidebar >
       <UserMenus />
    </Sidebar>

      <CenterSpacer />

      <Main>
      <Title></Title>

      <SelectBox 
     name={"type"}
     id={"type"}
     label={""}
     options={types}
     select={setType}
     value={type}
     />

      <TextInput
      label={""}
      placeholder={""}
      type={"text"}
      required={true}
      name={"subject"}
      id={"subject"}
      className={"subject"}
      autocomplete={"off"}
      value={subject}
      onChange={inputSubject}
     />  

<TextArea onChange={inputText} value={text} />
{/* 
     <Button onClick={() => dispatch(saveNotification({
       id: id,
       type: type,
       subject: subject,
       text: text
     }))}>
       
     </Button> */}

     <PrimaryButton 
      style={'primary__red'} onClick={() => dispatch(saveNotification({
       id: id,
       type: type,
       subject: subject,
       text: text
      }))} label={''}
     />

      </Main>
    </Container>
</div>
</div>

 )
}

export default NotificationForm;