import React from 'react';
import styled from 'styled-components';

const PreviewImg  = styled.img`
  width: 300px;
  height: 300px;
  border-radius: 1em;
  object-fit: cover; /*  */
`;

const ImagePreview = (props) => {
  
 return(
  <div onClick={() => props.delete(props.id)}>
      <PreviewImg src={props.path} alt=""/>
  </div>
 )
}

export default ImagePreview;