const SidebarData = (props) => {
    const dispatch = useDispatch();

    const selectMenu = (event, path) => {
        dispatch(push(path));
        // props.onClose(event)
      }

        // firebase   (useState) 
        const [filters, setFilters] = useState ([
            {func: selectMenu, label: "", id : "all", value:"/"}
            // {func: selectMenu, label: "",    id: "male",   value: "/?gender=male"  },
            // {func: selectMenu, label: "", id: "female", value: "/?gender=female"},
          ])
      
    
     //  fairebase  useEffect 
     useEffect(() => {
        db.collection('categories')
        .orderBy('order' , 'asc')
        .get()
        .then(snapshots => {
          const list = []
          snapshots.forEach(snapshot => {
            //   js 
            const category = snapshot.data()
            list.push({func: selectMenu, label: category.name, id: category.id, value: `/products/?category=${category.id}`})
          })
          setFilters(prevState => [...prevState, ...list])
        })
      }, []);
    

 return(
  <>
     {filters.slice(2, 8).map(filter => (
                   <Category
                    key={filter.id}
                    onClick={(e) => filter.func(e, filter.value)}
                   >
                   {filter.label}
                   </Category>  
                 ))}
  </>
 )
}

export default SidebarData;
