import styled from 'styled-components';
import * as AiIcons from "react-icons/ai";
import {useSelector} from "react-redux";
import {getUserId} from '../../reducks/users/selectors';
import {db} from '../../firebase/index';

const media = {
  sp: '@media(max-width: 650px)'
}

const ProductCard = styled.div`
position: relative;
/* height: 22.2vh; */
/* height: 12.5em; */
      border: 1px solid #ccc;
      width: 100%;
      border-radius:0.5em;
      max-width:35em;
      margin: 1.5em auto 0;
      background-color: #fff;
      transition: all ease-out .3s;
     cursor: pointer;
   :hover {
       background-color: #f5f5f5;
       transition: all ease-out .3s; 
   }
   /* ${media.sp} {
            height: 20.5vh;
   } */
   img {


       width: 100%;
       height: 100%;
       max-width: 200px;
       max-height: 200px;
       

       object-fit: cover;
       border-radius: 0.5em 0 0 0.5em;

   }
   h3 {

       font-size:1em;
       font-weight:bold;
       padding: 0;
   }
   /* ${media.sp} {
    height: 8.95em;
   } */
`;

const TextArea = styled.div`
 /* padding: 0 5vw; */
 width:15em;
 margin: 0 1em;
 P {
   padding: 0;
 }
`;

const Item = styled.div`
 /* position: absolute; */
 display: flex;
      /* justify-content: center; */
      align-items: center;
      figure{
        height: 100%;
        width: 100%;
        max-height: 200px;
        max-width: 200px;

      }

`;

const Icon = styled.div`
/* position: absolute;
right: 1em; */
   ${media.sp} {
     display: none;
   }
`;

const PcIcon = styled.div`
display: none;
${media.sp} {
  margin-top: 0.3em;
     display: block;
     font-size: 2.1em;
    cursor: pointer;
    transition: all ease-out .3s;
   }
`;

const CartItem = (props) => {
    const selector = useSelector((state) => state);
    const uid = getUserId(selector);

    const image = props.product.images[0].path;
    const name = props.product.name;
    const price = props.product.price.toLocaleString();
    // const size = props.product.size;

    // console.log(size)

  const removeProductFromCart = (id) => {
    return db.collection('users').doc(uid)
             .collection('cart').doc(id)
             .delete()
};

console.log(name)
// console.log(size)
 return(
     <ProductCard>
      <Item>
        <figure>
      <img src={image} alt="" />
      </figure>
         <TextArea>
             <h3>{name}</h3>
             <p>{"" + price}</p>
             {/* <p>{'size' + size}</p> */}
             <PcIcon>
         <AiIcons.AiFillDelete onClick={() => removeProductFromCart(props.product.cartId)}  />
         </PcIcon>
         </TextArea>
         <Icon>
         <AiIcons.AiFillDelete className="product-delete-icon" onClick={() => removeProductFromCart(props.product.cartId)}  />
         </Icon>
      </Item>
   </ProductCard>
 )
}

export default CartItem;
