import React, {useEffect} from 'react';
import {ProductCard} from '../components/Products';
import {useDispatch, useSelector} from "react-redux";
// import {useDispatch} from "react-redux";
import {getProducts} from "../reducks/products/selectors";
import {fetchProducts} from "../reducks/products/operations";
import styled from 'styled-components';
// import {Pagenation} from '../components/UIkit';

const Products = styled.div`
    display:flex;
    flex-wrap: wrap;
    justify-content:center;
    padding-top:3em;
`;

const ProductList = () => {
    const dispatch = useDispatch()
    const selector = useSelector(state => state);
    const products = getProducts(selector)

    // querygendercategory  
    // query  ?gender= 
    const query = selector.router.location.search;
    // gendercategory .test  (?gender=)  
    // const gender = /^\?gender=/.test(query) ? query.split('?gender=')[1] : "";
    const category = /^\?category=/.test(query) ? query.split('?category=')[1] : "";
    // const search = /^\?search=/.test(query) ? query.split('?search=')[1] : '';

    useEffect(() => {
        dispatch(fetchProducts(category));
    },[query])

    
    console.log(products)

    return (
        <div className="common__item">
       <div className="common__center">
       <h2></h2>
       <Products>
                {/* {products.length > 0 && (
                    products.map(product => ( <ProductCardnkey={product.id} id={product.id} name={product.name} images={product.images} price={product.price} />))
                )} */}
                {products.length > 0 ? (
    products.map(product =>  <ProductCard key={product.id} id={product.id} name={product.name} images={product.images} price={product.price} /> )
  ) : (
      <p>!!</p>
  )}
        </Products>
        {/* <Pagenation /> */}
        </div>
        </div>
    );
};

export default ProductList;




 