//  Auth
export const listenAuthState = () => {
    return async (dispatch) => {
        return auth.onAuthStateChanged(user => {
            if(user) {
               // StoreState 
               const uid = user.uid

               db.collection('users').doc(uid).get()
               .then(snapshot => {
                   const data = snapshot.data()

                   dispatch(signInAction({
                       customer_id: (data.customer_id) ? data.customer_id : "",
                       payment_method_id: (data.payment_method_id) ? data.payment_method_id : "",
                       isSignedin: true,
                       role: data.role,
                       uid: uid,
                       username: data.username,
                       email: data.email,
                       prefecture: data.prefecture,
                       city: data.city,
                       other: data.other,
                       description: data.description,
                       post: data.post,
                    //    images: data.images,
                       
                   }))

                //    dispatch(push('/'))
               })

            } else {
                // 
                dispatch(push('/signin'))
            }
        })

    }
}

// userEdit
export const userEdit = ({id,username, email, prefecture, city, other, post, description,images}) => {
    return async (dispatch) => {
      const timestamp = FirebaseTimestamp.now()

      if (username === "" || email === "" || prefecture === "" || city === "" || other === "" || post === "" || description === "") {
        alert("")
        return false
    }

      const data = {
        created_at: timestamp,
        email: email,
        role: "customer",
        // uid: uid,
        updated_at:  timestamp,
        username: username,
        prefecture: prefecture,
        city: city,
        other: other,
        post: post,
        description: description,
        images: images
      }
      

      //  id 
//   if (id === "") {
//         //   id
//         const ref = usersRef.doc();
//         //   id
//         id = ref.id
//         //   id
//           data.id = id
//         //   timestamp
//           data.created_at = timestamp
//   }

    //   Firebasesetdispatchhome   {marge: true} 
      // return productsRef.doc(id).set(data, {marge: true})
      // 2 .set(data)  
      return usersRef.doc(id).set(data, {merge: true})
      .then(()=>{
          dispatch(push('/user'))
      }).catch((error) => {
          throw new Error(error)
      })
    }
}
