import React, {useState,useCallback} from 'react'
import {ProductInput,PrimaryButton} from '../../components/UIkit';
import * as CgIcons from "react-icons/cg";
import * as GrIcons from "react-icons/gr";
import styled from 'styled-components';

const Item = styled.div`
display:flex;
align-items:center;
justify-content: center;
`;

const Button = styled.button`
margin: 0 auto;
padding:1em;
`;

const Center = styled.div`
text-align:center;
`;

const ProductOther = (props) => { 
    const [inputFields, setInputFields] = useState([
        { firstName: '',lastName: ''},
    ]);
    const [firstName, setFirstName] = useState(0);
    const [lastName, setLastName] = useState(0);

    const inputFirstName = useCallback((event) => {
        setFirstName(event.target.value)
    }, [setFirstName]);

    const inputLastName = useCallback((event) => {
        setLastName(event.target.value)
    }, [setLastName]);

    // 
    const handleSubmit = (e) => {
        // console.log 
        e.preventDefault(); 
        console.log("InputFields", inputFields);
    };

    const handleChangeInput = (index,event) => {
       console.log(index,event.target.name);
    //    
    const values = [...inputFields];
    values[index][event.target.name] = event.target.value;
    setInputFields(values);
    }

    // index 
    const handleAddFields = () => {
        //  
        setInputFields([...inputFields, { firstName: '', lastName: '' }])
    }

        // index 
    const handleRemoveFields = (index) => {
        const values = [...inputFields]
        values.splice(index,1);
        setInputFields(values);
    }

    return(
    <div>
        {/*  onSubmit onClick  */}
        <form onSubmit={handleSubmit}>
            { inputFields.map((inputField, index) => (
                <Item key={index}>
                    {/* <input type="text"/> */}
                    <ProductInput
                    label={""}
                    placeholder={""}
                    type={"text"}
                    required={true}
                    name={"firstName"}
                    id={"firstName"}
                    className={"firstName"}
                    autocomplete={"off"}
                    value={inputField.firstName,firstName}
                    onChange={event => handleChangeInput(index,event),inputFirstName}
                    />
                    
                    <ProductInput
                    label={""}
                    placeholder={""}
                    type={"text"}
                    required={true}
                    name={"lastName"}
                    id={"lastName"}
                    className={"lastName"}
                    autocomplete={"off"}
                    value={inputField.lastName,lastName}
                    onChange={event => handleChangeInput(index,event),inputLastName}
                    />

                    {/* <ProductInput
                    label={"LastName"}
                    placeholder={"LastName"}
                    type={"text"}
                    required={true}
                    name={"LastName"}
                    id={"LastName"}
                    className={"LastName"}
                    autocomplete={"off"}
                    value={inputField.lastName}
                    onChange={event => handleChangeInput(index,event)}
                    /> */}
                    {/* index  onClick={() => handleAddFields()} */}
                    <CgIcons.CgRemove className="InputIcon" onClick={(index) => handleRemoveFields()}/>
                    {/* index  onClick={() => handleAddFields()} */}
                    <GrIcons.GrAddCircle className="InputIcon" onClick={() => handleAddFields()} />
                </Item>
            )) }
        </form>
      <Center>
      <Button onClick={handleSubmit}></Button>
      </Center>
    </div>
    )
   
}

export default ProductOther;


