import React,{useCallback, useMemo} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {CartItem} from "../components/Products";
import styled from 'styled-components';
import {TextDetail,PrimaryButton} from '../components/UIkit';
import{getProductsInCart} from '../reducks//users/selectors';
import {orderProduct} from '../reducks/products/operations';


const media = {
  sp: '@media(max-width: 650px)'
}

const Flex = styled.div`
    display:flex;
    justify-content:center;
    flex-wrap : wrap;
    /* justify-content:center;
    height: auto;
    align-items: flex-start;
    flex-wrap : wrap; */
    hr {
      margin: 0.5em 0;
    }
`;

const CartDetail = styled.div`
    margin: 0 2.5em;
    text-align:left;
    background-color: #f5f5f5;
    border-radius:0.5em;
    padding:2em 2.5em;
    display: inline-block;
    ${media.sp} {
    margin: 2em 0 0 0;
    width: 100%;
   }

`;


const OrderConfirm = () => {
    const selector = useSelector((state) => state);
    const dispatch = useDispatch();
    // selector 
    const productsInCart = getProductsInCart(selector);

    // ----------------------    
    // 
    //  useMemo   
    // .reduce sum(0) 
    // ----------------------
  const subTotal = useMemo(() => {
    return productsInCart.reduce((sum, product) => sum += product.price, 0)
  }, [productsInCart]);

    // ----------------------    
    // 
    // ----------------------
    const shippingFee = (subTotal >= 10000) ? 0:210;

    // ----------------------    
    // 
    // ----------------------
    const tax = subTotal * 0.1;

    // ----------------------    
    // 
    // ----------------------
    const total = subTotal + shippingFee + tax;

    // ----------------------    
    // orderProduct(operation) 
    // ----------------------
    const order = useCallback(() => {
        dispatch(orderProduct(productsInCart,total))
    },[productsInCart,total])

    console.log(order)


 return(
    <div className="common__item">
       <div className="common__center">
           <h2></h2>
            <Flex>
             <div>
             {productsInCart.length > 0 && (
                  productsInCart.map(product => <CartItem key={product.id} product={product} /> )
              )}
             </div>
             <CartDetail>
              <TextDetail label={""} value={"" + subTotal.toLocaleString()} />
              <TextDetail label={""} value={"" + tax} />
              <TextDetail label={""} value={"" + shippingFee.toLocaleString()} />
              <hr />
              <TextDetail label={"()"} value={"" + total.toLocaleString()} />
             </CartDetail>
            </Flex>
            {/* <button onClick={order}></button> */}

            <PrimaryButton 
      style={'primary__red'} onClick={() => dispatch(order)} label={''}
     />
    </div>
    </div>
 )
}

export default OrderConfirm;