import React, { useEffect,useState,useCallback } from 'react';
import { db, FirebaseTimestamp } from '../firebase/index';
import { useSelector,useDispatch } from "react-redux";
// import {ImageArea} from '../components/UIkit';
import styled from 'styled-components';
import {ProductButton,ImageSwiper,SizeTable} from '../components/Products';
import * as AiIcons from "react-icons/ai";
import * as FaIcons from "react-icons/fa";
import * as MdIcons from "react-icons/md";
import * as RiIcons from "react-icons/ri";
import {IconContext} from 'react-icons';
import {addProductToCart, addProductToLike} from '../reducks/users/operations';
import {getUserId} from '../reducks/users/selectors';
import HTMLReactParser from 'html-react-parser';

const media = {
  sp: '@media(max-width: 650px)'
}

const CenterSpacer = styled.div`
 margin: 0 1em;
`;


const Table = styled.div`
 display:flex;
 align-items: center;
 text-align:center;
 justify-content: center;
`;

const Text = styled.p`
 padding-left:0.8em;
`;

const Head = styled.div`
 display:flex;
 align-items: center;
 
  p{
      padding-left:0.5em;
  }
`;

const Contents = styled.div`
   width: 50%;
   ${media.sp} {
    width: 100%;
   }
   h2 {
       padding-top:0;
   }
`;

const ContentsPage = styled.div`
   display:flex;
   justify-content:center;
   padding-top:5em;
   ${media.sp} {
    flex-wrap : wrap;
   }
`;

const Price = styled.p`
  font-size: 2.5em;
  font-weight:bold;
`;




const ProductDetail = () => {
    // State
    const selector = useSelector((state) => state);
    const dispatch = useDispatch();
    const uid = getUserId(selector);
    //  Syore  URL  pathname 
    const path = selector.router.location.pathname;
    // id 2pathname(id)
    const id = path.split('/product/')[1];

    // const ButtonLists = [
    //     {
    //       label: '',
    //       color: 'button__red',
    //       path: '/signup',
    //       icon: <FaIcons.FaCartPlus />
    //     },
    //     {
    //       label: '',
    //       path: '/signin',
    //       color: 'button__pink',
    //       icon: <AiIcons.AiOutlineHeart />
    //       }
    //   ];

    // useState setProduct  product  
    const [product,setProduct] = useState(null);

    const [isLiked, setIsLiked] = useState(false)

    // -------------------------------------
    //  Firebase  
    // (return  useEffect )
    // -------------------------------------
    useEffect(() => {
        // collection  products  id 
        // id  selecor  path 
        db.collection('products').doc(id).get()
        //    
        .then(doc => {
            const data = doc.data();
            // useState  setProduct data  product 
            setProduct(data)
        })
    }, []);
    
    // const uid = getUserId(selector)
    // const username = getUsername(selector)

        //   
        // const images = (props.images.length > 0) ? props.images : [{path:NoImage}]
    
    // -------------------------------------
    //  AddProduct
    //  useCallBack  
    // -------------------------------------     
    const addProduct = useCallback((selectedSize) => {
      const timestamp = FirebaseTimestamp.now();
      dispatch(addProductToCart({
        added_at: timestamp,
        description: product.description,
        images: product.images,
        name: product.name,
        price: product.price,
        productId: product.id,
        quantity: 1,
        // sizes: product.sizes,
        size: selectedSize,
        state: product.state,
        category: product.category,
        shippingArea: product.shippingArea,
        shippingFee: product.shippingFee,
        shippingMethod: product.shippingMethod,
        shippingDay: product.shippingDay,

      }))
    },[product])

    // -------------------------------------
    //  AddLike
    //  useCallBack  
    // -------------------------------------     
    const addLike = useCallback(() => {
      if(!isLiked) {
        setIsLiked((isLiked) => !isLiked)
        const timestamp = FirebaseTimestamp.now();
        dispatch(addProductToLike({
          added_at: timestamp,
          description: product.description,
          images: product.images,
          name: product.name,
          price: product.price,
          productId: product.id,
          quantity: 1,
        }))
      } else {
        setIsLiked((isLiked) => !isLiked)
        return db.collection('users').doc(uid)
        .collection('like').doc(id)
        .delete()
      }
    },[product,isLiked])

    // -------------------------------------
    //  returnCodeToBr
    //  HTML-react-parser   <br> 
    // -------------------------------------  
   const returnCodeToBr = (text) => {
     if(text === "") {
       return text
     } else {
       return HTMLReactParser(text.replace(/\r?\n/g,'<br />'))
     }
   };

 return(
  <div className="common__item">
  <div className="common__center">
    <h2></h2>
     {/* product  */}
         {product && (
            // <div className="common__center">
                <ContentsPage>
              <Contents>
              <ImageSwiper images={product.images} />
             </Contents>
             <CenterSpacer />
             <Contents>
              <h2>{product.name}</h2>
              <Price>{product.price.toLocaleString()}</Price>
{/* 
            {ButtonLists.map((Lists,List) => {
            return <ProductButton addProduct={addProduct} key={List} label={Lists.label} color={Lists.color} path={Lists.path} icon={Lists.icon} />
        })} */}

{/* <ProductButton sizes={product.sizes} product={product} addProduct={addProduct} label={''} color={'button__red'} icon={<FaIcons.FaCartPlus />} /> */}
<ProductButton addProduct={addLike} label={''} color={'button__pink'} icon={<AiIcons.AiOutlineHeart />} />
              <p>{returnCodeToBr(product.description)}</p>
              <SizeTable addProduct={addProduct} product={product} sizes={product.sizes} />
              <hr />

              <IconContext.Provider value={{color: '#747474'}}>
              {/* <Table>
               <Head>
               <AiIcons.AiFillFolder />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.category}</Text>
               </Table> */}

               <Table>
               <Head>
               <AiIcons.AiFillEye />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.state}</Text>
               </Table>

               {/* <Table>
               <Head>
               <FaIcons.FaToolbox />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.quantity}</Text>
               </Table> */}

               <Table>
               <Head>
               <FaIcons.FaMoneyBillWaveAlt />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.shippingFee}</Text> 
               </Table>

               <Table>
               <Head>
               <MdIcons.MdLocalShipping />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.shippingMethod}</Text> 
               </Table>

               <Table>
               <Head>
               <AiIcons.AiOutlineFieldTime />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.shippingDay}</Text>
               </Table>

               <Table>
               <Head>
               <RiIcons.RiMapPinLine />
                 <p><strong></strong></p>
                </Head>
                <Text>{product.shippingArea}</Text>
               </Table>

               </IconContext.Provider>

               <hr />

              {/* {TableLists.map((Lists,List) => {
            return <ProductTable key={List} icon={Lists.icon} name={Lists.name} text={Lists.text} />
        })} */}

              {/* <p>{product.state}</p> */}
        {/* <p>{product.categorys}</p> */}
        {/* <p>{product.shippingArea}</p> */}
        {/* <p>{product.shippingFee}</p> */}
        {/* <p>{product.shippingMethod}</p> */}
        {/* <p>{product.shippingDay}</p> */}

             </Contents>
             </ContentsPage>
            // </div>
            
         )}
      
     </div>
     </div>
 )
}

export default ProductDetail;